  gap: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.photo-card {
  transition: transform .25s ease-in;
}

.photo-card:hover {
  transform: scale(1.03);
}

.image__photo-card {
  width: 300px;
  height: 191.083px;
  object-fit: cover;
  display: block;
}

.info {
  border-radius: 0 0 4px 4px;
