}

.search-form {
  text-align: center;
}

.container {
  max-width: 100%;
  padding: 30px;
}

.loader {
  max-width: 100%;
  min-height: 60px;
  max-height: 60px;
  height: 5vh;
  z-index: 1;
  flex-basis: 100%;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.loader.hidden {
  display: none;
}

.loader-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.ball {
  position: absolute;
}

.loader :before, .loader :after {
  content: "";
  box-sizing: border-box;
  position: absolute;
}

@media screen and (min-width: 700px) {
  .loader {
    max-width: 33.33%;
    flex-basis: 33.33%;
  }
}

